<!---->
<style lang='scss' rel='stylesheet/scss'>
    @import "../../../utils/mixins/common";
    @import "../../../utils/mixins/mixins";

    #coursetask-addcoursetamplte {
        @extend %content-container;
        background: #fff;
        padding: 20px;
        .el-form {
            width: 60%;
            img {
                width: 35%;
                // height: 50% 
            }
        }
        /*.search {
            @extend %top-search-container;
        }*/
        .collection {
            align-items: center;
            min-height: 36px;
            border-radius: 4px;
            padding: 3px 10px;
            border: 1px solid #bfcbd9;
        }
        .collection {
            align-items: center;
            min-height: 36px;
            border-radius: 4px;
            padding: 3px 10px;
            border: 1px solid #bfcbd9;
        }
        .u-course-tag {
            margin-right: 10px;
            &:last-child {
                margin-right: 0;
            }
        }
        .el-tabs__content {
            position: relative;
            .dialog-select-item {
                h5 {
                    line-height: 40px;
                    font-size: 14px;
                    padding-left: 20px;
                    background: #fbfdff;
                    border: 1px solid #d1dbe5;
                    border-bottom: none;
                }
                position: absolute;
                top: 54px;
                right: 120px;
                height: 500px;
                width: 42%;
                display: inline-block;
                vertical-align: top;
            }
        }
        .row-class {
            border: 1px solid #d1dbe5;
        }

        .course-search {
            margin-bottom: 12px;
            .el-input {
                width: 150px;
                ::-webkit-input-placeholder {
                    font-size: 13px
                }
            }
        }
    }
</style>

<template>
    <article id="coursetask-addcoursetamplte">
        <el-form :model="form" :rules="rules" label-position="right" ref="form" label-width="120px" style="width: 60%">
            
            <el-form-item prop="title" label="">
                <el-input v-model="form.title" auto-complete="off"></el-input>
                
            </el-form-item>

            <el-form-item prop="description" label="">
                <el-input v-model="form.description" auto-complete="off" :rows="6" type="textarea"></el-input>
            </el-form-item>
            <el-form-item prop="imgUrl" label="">
                <div class="img-wrap" v-if="form.image">
                    <img :src="form.image | fillImgPath" alt=""/>
                </div>
                <ImagEcropperInput :isRound="false" :aspectRatio="2.15" :confirmFn="cropperFn"
                                   class="upload-btn"></ImagEcropperInput>
            </el-form-item>
            <el-form-item prop="course" label="">
                <el-tag style="margin-right: 3px"
                        v-for="(c,index) in form.course" :key="index"
                        :closable="true"
                        @close="form.courses.splice(index,1)"
                        type="success">
                    {{c.name}}
                <!--c-->
                </el-tag>
                <el-button type="primary" @click="dialogCourse.isShow=true" size="small"></el-button>
            </el-form-item>
            <el-form-item prop="sort" label="">
                <el-input-number v-model="form.sort" auto-complete="off"></el-input-number>
            </el-form-item>
            <el-form-item label="">
                <DateRange :start="form.create_start" :end="form.create_end" @changeStart="val=> form.create_start=val "
                    @changeEnd="val=> form.create_end=val" >
                </DateRange>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select clearable v-model="form.type" @change="choosePushType" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item 
                :label="pushTypeDialog.title" 
                v-if="form.type && form.type===pushTypeDialog.type">
                <div class="collection" @click="openPushTypeDialog">
                    <el-tag 
                        class="u-course-tag"
                        v-for="item in pushTypeDialog.selectedData[this.pushTypeDialog.type]" 
                        :key="item.id">
                        {{item.name}}
                    </el-tag>
                </div>
            </el-form-item>
            <!--<el-form-item prop="gov_ids" label="">
                
            </el-form-item>
            <el-form-item prop="user_ids" label="">
                
            </el-form-item>-->
            <el-form-item>
                <el-button type="primary" @click="submit(0)"></el-button>
                <!--<el-button type="warning" @click="submit(1)"></el-button>-->
            </el-form-item>
        </el-form>

        <!--  -->
        <dialogSelectData ref="dialogSelect" v-model="dialogCourse.isShow" :getData="fetchCourse" title=""
                          :selectedList="form.course" @changeSelected="val=>form.course=val">
            <div slot="search" class="course-search">
                <el-input @keyup.enter.native="$refs.dialogSelect.fetchCourse(true)" v-model="dialogCourse.keyword"
                          icon="search"
                          placeholder=""></el-input>
            </div>
        </dialogSelectData>
        <!--  -->
        <el-dialog 
            :title="pushTypeDialog.title" 
            :visible.sync="pushTypeDialog.showDialog" 
            v-if="pushTypeDialog.showDialog">
            <template v-if="pushTypeDialog.isSearch">
                <section class="search">
                    <section>
                        <i></i>
                        <DepSelect v-model="pushTypeDialog.form.department_id" :change="getPushTypeData"></DepSelect>
                    </section>
                </section>
            </template>
            <Transfer placeholder=""
                      @searchFn="(val)=>{pushTypeDialog.page=1;pushTypeDialog.form.keyword=val;fetchPushTypeData();}"
                      @moreFn="()=>{pushTypeDialog.page++;fetchPushTypeData('no-clear');}" 
                      :total="pushTypeDialog.total"
                      :data="pushTypeDialog.data"
                      v-model="pushTypeDialog.selectedData[pushTypeDialog.type]"></Transfer>
            <span slot="footer" class="dialog-footer">
                <el-button @click="pushTypeDialog.showDialog = false"> </el-button>
                <el-button type="primary" @click="transferConfirmFn"> </el-button>
            </span>
        </el-dialog>

    </article>
</template>

<script>
    import Transfer from '../../component/dialog/Transfer.vue'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInput.vue'
    import courseTaskService from '../../../services/gov/courseTaskService.js'
    import courseService from '../../../services/course/courseService.js'
    import govService from '../../../services/gov/govService.js'
    import userService from '../../../services/gov/userService.js'
    import dialogSelectData from '../../component/dialog/SelectData4table.vue'
    import DateRange from '../../component/form/DateRangePicker.vue'


    export default{
        name: 'coursetask-template-add',
        data () {
            return {
                form: {                // 
                    id: void 0,
                    title: void 0,          // 
                    category_id: void 0,       // 
                    image: void 0,        // 
                    description: void 0,  // 
                    sort: void 0,         // 
                    course_id: void 0,     // 
                    status: void 0,       // 
                    category_name: '', // 
                    course: [],
                    type:void 0,       //
                },
                rules: {
                    title: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    category_id: {type: 'number', required: true, message: '', trigger: 'change'}
                },
                dialogCourse: {
                    loading: false,
                    isShow: false,
                    keyword: void 0,
                },
                pushTypeDialog: { //
                    fetchParam: {
                        department_id: '',
                        keyword: ''
                    },
                    title: '',
                    isSearch: '',
                    type: '',
                    showDialog: false,
                    selectedData: {
                        user: [],
                        user_group: [],
                        department: []
                    },
                    data: [],
                    page: 1,
                    page_size: 15,
                    total: 0,
                },
            }
        },
        created () {
            xmview.setContentLoading(false)
            if (this.$route.params.item) {
                this.form = this.$route.params.item
                xmview.setContentTile('-')
                this.choosePushType()
            }
        },
        methods: {
            transferConfirmFn () {
                this.pushTypeDialog.showDialog = false
            },
            //
            openPushTypeDialog () {
                this.pushTypeDialog.showDialog = true
                this.pushTypeDialog.page = 1
                this.pushTypeDialog.fetchParam.department_id = ''
                this.pushTypeDialog.fetchParam.keyword = ''
                this.fetchPushTypeData()
            },
            //
            choosePushType(){
                let map = {
                    1: {
                        type: '1',
                        label: '',
                        isSearch: false,
                    },
                    2: {
                        type: '2',
                        label: '',
                        isSearch: true,
                    }
                }
                let param = map[this.form.type]
                // console.log(param)
                this.pushTypeDialog.title = param.label
                this.pushTypeDialog.isSearch = param.isSearch
                this.pushTypeDialog.type = param.type
            },
            getPushTypeData () {
                this.pushTypeDialog.page = 1
                this.fetchPushTypeData()
            },
            fetchPushTypeData (type) {
                type !== 'no-clear' && (this.pushTypeDialog.data = [])
                let map = {
                    1: govService.getSelectList,  //
                    2: userService.fetchData,  // 
                    // user_group: userService.userGroupSearch
                }
                let param = {
                    keyword: this.pushTypeDialog.fetchParam.keyword,
                    page: this.pushTypeDialog.page,
                    page_size: this.pushTypeDialog.page_size
                }
                
                if (this.pushTypeDialog.isSearch) {
                    alert(222)
                    console.log(param)
                    param.department_id = this.pushTypeDialog.fetchParam.department_id
                }
                map[this.pushTypeDialog.type](param).then(ret => {
                    this.pushTypeDialog.total = ret.total
                    if (this.pushTypeDialog.data.length > 0 && this.pushTypeDialog.data.length < ret.total) {
                        this.pushTypeDialog.data.splice(-1, 1)
                    }
                    this.pushTypeDialog.data.push(...ret.data, {id: -1})
                })
            },
            cropperFn(data, ext) {
                courseTaskService.CourseTaskTemplateUploadUrl({
                    avatar: data,
                    alias: `${Date.now()}${ext}`
                }).then((ret) => {
                    xmview.showTip('success', '')
                    this.form.image = ret.data.url // 
                }).catch((ret) => {
                    xmview.showTip('error', ret.message)
                })
            },
            fetchCourse (params) {
                return courseService.getPublicCourselist(Object.assign({}, this.dialogCourse, params))
            },
            submit(s) {
                this.$refs.form.validate((valid) => {
                    if (!valid) {
                        return false
                    }
                    // id
                    this.form.course_id = []
                    this.form.course.forEach((c) => {
                        this.form.course_id.push(c.id)
                    })
                    this.form.course_id = this.form.course_id.join(',')
                    if (s > 0) {
                        this.form.status = s
                    }
                    let reqFn = courseTaskService.addCourseTaskTemplate
                    if (this.form.id) {
                        reqFn = courseTaskService.updateCourseTaskTemplate
                    }
                    reqFn(this.form).then((ret) => {
                        xmview.showTip('success', '')
                        this.$router.back()
                    }).catch((ret) => {
                        xmview.showTip('error', ret.message)
                    })
                })
            }
        },
        components: {DateRange,ImagEcropperInput, dialogSelectData,Transfer}
    }
</script>



